* {
  margin: 0;
  padding: 0;
  font-family: Verdana, Arial, sans-serif;
}

.app {
  width: 700px;
  margin: 15px auto 35px;
}

.form {
  width: 100%;
  border: 1px solid #808080;
  display: flex;
  margin: 0 auto;
  padding: 10px 0;
  text-align: center;
  justify-content: space-around;
  align-items: center;
  font-size: 16px;
}
.select-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.dropdown {
  outline: none;
  border: 1px solid #808080;
  width: 120px;
  cursor: pointer;
  padding: 8px;
  margin-left: 3px;
  font-size: 14px;
}

.hero-list {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  /*background-color: rgb(193, 233, 213);*/
}

.hero-item {
  background-color: #FFF;
  margin: 15px auto;
  width: 100%;
  height: 150px;
  display: grid;
  grid-template-columns: 150px 1fr;
  grid-template-rows: 50px 1fr;
}

.hero-pic {
  grid-row: 1 / 3;
  border: 1px solid #808080;
}
.hero-pic img {
  max-width: 100%;
}

.hero-name, .hero-bio {
  border: 1px solid #808080;
  padding: 10px;
}

.hero-name {
  grid-column: 2 / -1;
  border-left: none;
  font-size: 10pt;
  line-height: 20pt;
  text-align: center;
}

.hero-bio {
  grid-column: 2 / -1;
  border-left: none;
  border-top: none;
}

.pagination {
  width: 100%;
  margin: 12px auto;
}
.pagination-list {
  list-style: none;
  text-align: center;
}
.page-item {
  display: inline-block;
  margin: 0 10px;
}
.page-link {
  text-decoration: none;
  font-size: 12pt;
  color: #333;
  padding: 7px;
}
.page-link:hover {
  background-color: #808080;
  color: #FFF;
}